.content-container{
    display: flex;
    flex-direction: row;
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: 10vh;
    justify-content: space-around;
    flex-wrap: wrap;
}

.content-text{
    text-align: left;
    width: 50%;
}

.text-body{
    display: block;
}

.text-title{
    display: flex;
    gap: 20px;
}

h4{
    color:#2631f5;
    background-color: #fafefa;
    font-size: 1.5rem;
    font-style: normal;
}

.text-body p{
    margin-top: 2vh;
    font-weight: 400;
    margin: 0;
    color: black;
    margin-bottom: 2vh;
}

.text-body h2{
    margin-bottom: 2vh;
    color:#fafefa;
    background-color: #2631f5;
    width: fit-content;
    padding: 4px;
    font-style: normal;

}

.content-images{
    margin-top: 11vh;
}

.text-body h3{
    margin-bottom: 2vh;
    background-color:#fafefa;
    color: #2631f5;
    width: fit-content;
    font-weight: 400;
    font-style: normal;
}