@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,400;0,500;0,600;1,400&display=swap);
* {
  box-sizing: border-box;
  margin:0;
  padding:0;
  
}


html {
  font-family: 'IBM Plex Sans', sans-serif;
}

body {
  margin: 0;
  color: #2631f5;
  background-color: #fafefa;
  text-align: center;
  font-style: italic;
  font-weight: 500;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
 
}

body::-webkit-scrollbar {
  display: none;
}


.header-name{
    font-size: 24px;
    margin-top: 2vh;
    font-style: italic;
}

a{
    color: #2631f5;
}

.header-name:visited{
    font-size: 24px;
    margin-top: 2vh;
    color: #2631f5;
}
.header-main-container{
    display: inline-flex;
    flex-wrap: wrap;
    margin-top: 2vh;
    grid-gap: 30px;
    gap: 30px;
    margin-left: 10vw;
    margin-right: 10vw;
    justify-content: center;
    font-weight: 400;
    font-style: normal;
    margin-bottom: 30px;
    color: rgb(46, 46, 46);
}

.header-main-container:visited {
    color: rgb(46, 46, 46);
}

.header-main-container a{
    color: rgb(46, 46, 46);
}
.about-container{
    margin-left: 10vw;
    margin-right: 10vw;
    line-height: 27px;
    text-align: left;
    margin-top: 10vh;
    font-style: normal;
    font-size: 1rem;
    display: flex;
    justify-content: flex-start;
    grid-gap: 200px;
    gap: 200px;
    flex-wrap: wrap;
    margin-bottom: 10vh;

}


.about-text{
    margin-top: 2vh;
    font-weight: 400;
    width: 60%;
    margin: 0;
    color: black;
}

.about-text::selection{
    margin-top: 2vh;
    font-weight: 400;
    width: 60%;
    margin: 0;
    color: #fafefa;
    background-color: #2631f5;
}


.about-text h2{
    margin-bottom: 2vh;
    color:#fafefa;
    background-color: #2631f5;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 2px;

}

*::selection{
    color: #fafefa;
    background-color: #2631f5;
}

.prof-links{
    display: flex;
    flex-direction: column;
    grid-gap: 100px;
    gap: 100px;
    margin-top: 2%;
    justify-content: space-around;

}

.CV-icon{
    font-size: 1.5rem;
}

.Email-icon{
    color: black;
}

.Email-icon:hover{
    color: black;
    cursor: grab;
}

hr{
    border-color:#2631f5;
    border-style: solid;
    border-width: 2px;
    width: 10%;
    margin-bottom: 2vh;
}

.igIcon{
    padding:0px;
    cursor: pointer;
}

span{
    font-size: 1.5rem;
    margin-left: 10px;
}


.cv-update{
    font-size: 0.7rem;
    font-style: italic;
}

.doc-emoji{
    cursor: pointer;
    text-decoration: none;
}

a{
    text-decoration: none;
}


@media only screen and (max-width: 1160px) {
    .about-container {
        display: flex;
        flex-direction: column;
        justify-content: none;
        grid-gap:10px;
        gap:10px;
    }

    .about-text{
        width: 100%;
    }

    .prof-links{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
  }

  @media only screen and (max-width: 762px) {
    .about-container {
        display: flex;
        flex-direction: column;
        justify-content: none;
        grid-gap:0px;
        gap:0px;
        margin-top: 0;
    }

    .about-text{
        width: 100%;
        order: 2;
    }

    .prof-links{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 5vh;
        grid-gap:10px;
        gap:10px;
        align-items: stretch;
        order: 1;
        margin-bottom: 4vh;
        flex-wrap: wrap;
    }
  }

.work-main-container{
    margin-left: 10vw;
    margin-right: 10vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-top: 10vh;
    grid-gap: 80px 20px;
    gap: 80px 20px;
    align-content: space-around;
    margin-bottom: 10vh;
}


@media only screen and (max-width: 1160px) {
    .work-main-container{
        margin-left: 5vw;
        margin-right: 5vw;
        grid-gap: 40px 0px;
        gap: 40px 0px;
       
        display: flex;
        flex-direction: row;
    }
  }

@media only screen and (max-width: 900px) {
    .work-main-container{
        margin-left: 0;
        margin-right: 0;
        flex-shrink: 5;
    }
}
.work-card-container{
    display: block;
}

.work-card-image img{
    max-width: 80%;
    height: auto;
    border-radius: 6px;
    box-shadow: 5px 10px 30px black;
    transition: all .2s ease-in-out;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.work-card-image img:hover{
    max-width: 80%;
    height: auto;
    border-radius: 6px;
    box-shadow: 5px 10px 30px black;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.work-card-title{
    color: black;
    font-size: 0.9rem;
    font-style: normal;
    margin-top: 20px;
    text-align: left;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    justify-content: space-between;
}

.year{
    color: #fafefa;
    background-color: #f52626;
    font-style: italic;
    padding: 5px;
    border-radius: 10px;
    font-size: 0.7rem;
}



.blog-content-main{
    display: block;
    margin-bottom: 5vh;
}

.blog-content-title{
    display: flex;
    flex-direction: row;
    margin-bottom: 40px;
    font-size: 1.2rem;
    font-style: normal;
    justify-content: space-between;
    align-items:baseline;
    grid-gap: 10px;
    gap: 10px;

}

.blog-content-title hr{
    width: 80%;
}

.blog-content-body{
    display: flex;

    font-style: normal;

    color: black;
    text-align: left;
    grid-gap: 10px;
    gap: 10px;
    font-size: 1.0rem;
    font-weight: 400;
    justify-content: center;

}

.blog-content-body h2{
    color:#2631f5;
    margin-bottom: 1vh;
}

.bc-no{
    color:#fafefa;
    background-color: #f52626;
    padding: 2px;
}

.bc-date{
    padding-top: 10px;
}

.bb-text{
    width: 70%;
}

.bb-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.blog-content{
    display: flex;
    margin-left: 20vw;
    margin-right: 20vw;
    flex-direction: column;
    justify-content: center;
    margin-top: 10vh;
    margin-bottom: 10vh;
}
.content-container{
    display: flex;
    flex-direction: row;
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: 10vh;
    justify-content: space-around;
    flex-wrap: wrap;
}

.content-text{
    text-align: left;
    width: 50%;
}

.text-body{
    display: block;
}

.text-title{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

h4{
    color:#2631f5;
    background-color: #fafefa;
    font-size: 1.5rem;
    font-style: normal;
}

.text-body p{
    margin-top: 2vh;
    font-weight: 400;
    margin: 0;
    color: black;
    margin-bottom: 2vh;
}

.text-body h2{
    margin-bottom: 2vh;
    color:#fafefa;
    background-color: #2631f5;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 4px;
    font-style: normal;

}

.content-images{
    margin-top: 11vh;
}

.text-body h3{
    margin-bottom: 2vh;
    background-color:#fafefa;
    color: #2631f5;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-weight: 400;
    font-style: normal;
}
.other-main-content{
    display: flex;
    margin-left: 10vw;
    margin-right: 10vw;
    margin-top: 10vh;
    flex-direction: column;
    grid-gap: 100px;
    gap: 100px;
    margin-bottom: 10vh;
    
}



