@media only screen and (max-width: 810px) {
    .key-words-oc{
        display: flex;
        grid-gap: 10px;
        gap: 10px;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
        
        
    }

